import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import StarIcon from '../../assets/filters/Star_v1.svg'

const StyledRating = styled.div``

const RatingStar = styled(StarIcon)`
  width: 10px;
  margin-right: 1px;

  .star__inner {
    fill: var(--fill);
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(10)};
    margin-right: ${desktopVw(2)};
  }
`

const ProductRating = ({ className, rating }) => {
  return (
    <StyledRating className={className}>
      {[...Array(5)].map((_, i) => (
        <RatingStar
          key={i}
          style={{ '--fill': i < Math.round(rating) ? '#F0B44F' : 'unset' }}
          className='rating__star'
        />
      ))}
    </StyledRating>
  )
}

export default ProductRating
