import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import CartIcon from '../assets/cart.svg'
import TrackableButton from './TrackableButton'

const StyledQuickShopButton = styled(TrackableButton)`
  width: 33px;
  height: 33px;
  background: ${props => props.theme.colors.primary};
  border-radius: 50%;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(55)};
    height: ${desktopVw(55)};
    transform: scale(0.4);
    opacity: 0;
    transition: opacity 50ms, transform 250ms ease-out;
    transform-origin: center;
  }
`

const StyledCartIcon = styled(CartIcon)`
  fill: white;
  width: 8px;
  stroke-width: 1px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    opacity: 0;
    width: ${desktopVw(10)};
    transition: opacity 250ms ease-out;
    transition-delay: 100ms;
  }
`

const QuickShopButton = ({ className, handleClick, trackParams }) => {
  return (
    <StyledQuickShopButton
      className={`${className} quickshop-btn`}
      type='button'
      onClick={handleClick}
      trackParams={trackParams}
      aria-label='Open quick shop modal'
    >
      <StyledCartIcon className='quickshop-btn__icon' />
    </StyledQuickShopButton>
  )
}

export default QuickShopButton
